import React, { useEffect, useState } from 'react';
import { Loader } from '@googlemaps/js-api-loader';

interface CompanyProps {
  data: any[];
}

const Company: React.FC<CompanyProps> = props => {
  useEffect(() => {
    const loader = new Loader({
      apiKey: 'AIzaSyBI_6Bn1nUDu3o677tqEmmW1KV7UhnKkCU',
      version: 'weekly'
    });

    loader.load().then(() => {
      const { google } = window as any;

      if (
        !document.getElementById('map') ||
        !google ||
        !google.maps ||
        !google.maps.Map
      ) {
        return;
      }

      const lat: number = 35.67612894074747;
      const lng: number = 139.71010032475465;

      const map: any = new (window as any).google.maps.Map(
        document.getElementById('map') as HTMLElement,
        {
          mapId: '13c8241e9ab7d73d',
          center: {
            lat,
            lng
          },
          zoom: 18
        }
      );

      const image: string = 'https://daruma-inv.com/pin.svg?gegea';

      const marker: any = new google.maps.Marker({
        position: { lat, lng },
        map,
        icon: {
          url: image,
          scaledSize: new google.maps.Size(60, 60)
        }
      });
    });
  }, []);

  return (
    <section id='company' className='bg-gray'>
      <div className='container'>
        <h1>会社概要</h1>
        <div className='company-background bg-white'>
          <div className='row'>
            <div className='col-10 offset-1 col-lg-8 offset-lg-2'>
              <table className='table mt-4'>
                <tbody>
                  {props.data &&
                    props.data.map((d: any, i: number) => {
                      const thtdClassName: string =
                        i === 0 ? 'border-top-0' : '';

                      return (
                        <tr key={i}>
                          <th scope='row' className={thtdClassName}>
                            {d.key}
                          </th>
                          <td className={thtdClassName}>
                            <ul>
                              <li
                                dangerouslySetInnerHTML={{
                                  __html: d.value.replace(/\n/g, '<br />')
                                }}
                              />
                            </ul>
                          </td>
                        </tr>
                      );
                    })}
                </tbody>
              </table>
            </div>
          </div>
          <div
            id='map'
            className='w-100'
            style={{
              height: 360,
              filter: 'grayscale(100%)'
            }}
          />
        </div>
      </div>
    </section>
  );
};

export default Company;
